:root {
  --box-shadow-elevation-1: 0 2px 5px rgba(0, 0, 0, 0.1),
    0 1px 2px rgba(0, 0, 0, 0.25);
  --box-shadow-elevation-2: 0 5px 10px rgba(0, 0, 0, 0.15),
    0 3px 6px rgba(0, 0, 0, 0.24);
  --box-shadow-elevation-3: 0 10px 20px rgba(0, 0, 0, 0.2),
    0 6px 12px rgba(0, 0, 0, 0.23);
  --box-shadow-elevation-4: 0 15px 30px rgba(0, 0, 0, 0.25),
    0 10px 15px rgba(0, 0, 0, 0.22);
  --box-shadow-elevation-5: 0 20px 40px rgba(0, 0, 0, 0.3),
    0 15px 20px rgba(0, 0, 0, 0.21);

  --border-style: solid;
  --border-color: var(--card-border-color);
  --border-width: var(--sizing-border-width);
  --border: var(--border-width) var(--border-style) var(--border-color);
  --border-muted: var(--border-width) var(--border-style)
    var(--color-muted-border);
  --border-empty: var(--border-width) solid transparent;

  --card-box-shadow: var(--box-shadow-elevation-1);
  --card-background-color: var(--color-background);
  --card-inner-border-color: var(--color-muted-border);
}
